import React, { Component } from 'react';

import Picture from './Picture';

import ImgService from '../services/picture-service';

export default class Gallery extends Component {

	imgService = new ImgService();

	maxID = 100

	state = {
		images: []
	}

	componentDidMount() {
		this.getImg();
		this.getImg();
		this.getImg();
		this.getImg();
		this.getImg();
		this.getImg();
		this.getImg();
		this.getImg();
		this.getImg();
	}

	getImg() {
		this.imgService.getImage()
			.then((url) => {
				this.createItem(url)
			})
	}

	createItem(url) {

		const newImg = {
			url,
			id: this.maxID++,
			likes: Math.ceil(Math.random()*100),
			liked: false
		}

		this.setState(({ images }) => {
			const newArray = [ newImg, ...images ]

			return {
				images: newArray
			}
		})
	}

	onLike = (id) => {
		this.setState(({images}) => {
			const idx = images.findIndex((el) => el.id === id)

			const oldItem = images[idx]
			const newItem = { ...oldItem, liked: !oldItem.liked}
			if (!newItem.liked) {
				newItem.likes -= 1
			} else {
				++newItem.likes
			}
			
			const newArr = [
				...images.slice(0, idx),
				newItem,
				...images.slice(idx + 1),
			]
			return {
				images: newArr
			}
		})
	}

	render() {

		const { images } = this.state

		const galleryItem = images.map((item) => {
			return (
				<div key={item.id} className="gallery__item">
					<Picture info={ item } likeFn={ (id) => {this.onLike(id)} } />
				</div>
			)
		})

		return (
			<section className="gallery">
				<div className="gallery__inner inner">
					<div className="gallery__content">
						{ galleryItem }
					</div>
				</div>
			</section>
		);
	}
}